<template>
  <div>
    <el-table
      ref="pageTable"
      :data="tableData"
      :border="border"
      @selection-change="handleSelectionChange"
    >
      <slot></slot>
    </el-table>
    <div class="page-container">
      <el-pagination
        background
        layout="sizes, prev, pager, next"
        :current-page.sync="pageNum"
        :page-size="pageSize"
        :total="total"
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    border: {
      type: Boolean,
      default: true
    },
    getMethod: {
      type: Function,
      required: true
    }
  },
  data () {
    return {
      tableData: [],
      total: 0,
      pageSize: 10,
      pageNum: 1
    }
  },
  mounted () {
    this.getTableData()
  },
  methods: {
    async getTableData () {
      const { data } = await this.getMethod({
        pageNum: this.pageNum,
        pageSize: this.pageSize
      })
      const { list, total } = data
      this.tableData = list
      this.total = total
    },
    handleSelectionChange (val) {
      this.$emit('selection-change', val)
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.pageNum = 1
      this.getTableData()
      this.$refs.pageTable.clearSelection()
    },
    handleCurrentChange (val) {
      this.pageNum = val
      this.getTableData()
      this.$refs.pageTable.clearSelection()
    }
  }
}
</script>

<style scoped>
.page-container {
  margin-top: 10px;
  text-align: right;
}
</style>
